import styled from "styled-components";
import Task from "./Task";
import {
  DragDropContext,
  DragStart,
  DropResult,
  Droppable,
} from "react-beautiful-dnd";
import {
  TdragItem,
  Ttodo,
  selectedTaskState,
  taskDraggingState,
  taskFilterState,
  taskState,
  todoDraggingState,
} from "../atoms";
import { useRecoilState, useSetRecoilState } from "recoil";
import TaskOption from "./TaskOption";

type TdropAreaProps = {
  isDraggingTask?: boolean;
};

const Wrapper = styled.div`
  width: 100%;
  height: 100vh;
  background-color: ${(props) => props.theme.bgColor};
  display: grid;
  grid-template-columns: 1fr 3fr;
  grid-template-rows: 2fr 1fr;
  gap: 1em;
  & > * {
    margin: auto;
  }
  & > div {
    display: flex;
    gap: 1em;
  }
`;

const Title = styled.h1`
  font-size: 24px;
  font-weight: bold;
  color: white;
`;

const DropArea = styled.div<TdropAreaProps>`
  background-color: ${(props) => (props.isDraggingTask ? "pink" : "inherit")};
  display: flex;
  gap: 2em;
  padding: 2em;
  border-radius: 1em;
  transition: background ease-in-out 0.3s;
`;

const TaskDropArea = () => {
  const setTaskState = useSetRecoilState(taskState);
  const setTaskFilterState = useSetRecoilState(taskFilterState);
  const setSelectedTask = useSetRecoilState(selectedTaskState);
  const [isDraggingTodo, setTodoDraggingState] = useRecoilState(todoDraggingState);
  const [isDraggingTask, setTaskDraggingState] = useRecoilState(taskDraggingState);

  const onDragEnd = (args: DropResult) => {
    setTodoDraggingState(false);
    setTaskDraggingState(false);
    if (!args.destination) return;

    if (args.type === TdragItem.Todo) {
      let draggedTodo: Ttodo;

      setTaskFilterState(() => args.source.droppableId);
      setSelectedTask((prevTask) => {
        const newTodos = [...prevTask.todos];
        draggedTodo = prevTask.todos[args.source.index];
        const newTask = { ...prevTask };
        newTodos.splice(args.source.index, 1);
        newTask.todos = newTodos;
        return { ...newTask };
      });

      setTaskFilterState(() => args.destination.droppableId);
      setSelectedTask((prevTask) => {
        const newTodos = [...prevTask.todos];
        newTodos.splice(args.destination.index, 0, draggedTodo);
        const newTask = { ...prevTask };
        newTask.todos = newTodos;
        return { ...newTask };
      });
    }

    if (args.type === TdragItem.Task) {
      setTaskState((prevTasks) => {
        const newTasks = [...prevTasks];
        const draggedTask = newTasks.splice(args.source.index, 1)[0];
        newTasks.splice(args.destination.index, 0, draggedTask);

        return [...newTasks];
      });
    }
  };

  const onDragStart = ({ type }: DragStart) => {
    if (type === TdragItem.Todo) setTodoDraggingState(true);
    if (type === TdragItem.Task) setTaskDraggingState(true);
  };

  return (
    <Wrapper>
      <Title>this is DragAndDrop.js</Title>
      <DragDropContext onDragStart={onDragStart} onDragEnd={onDragEnd}>
        <Droppable
          droppableId="windowDropArea"
          type={TdragItem.Task}
          isDropDisabled={isDraggingTodo}
          direction="horizontal"
        >
          {(provided, snapshot) => {
            return (
              <DropArea
                ref={provided.innerRef}
                {...provided.droppableProps}
                isDraggingTask={isDraggingTask}
              >
                <Task />
                {provided.placeholder}
              </DropArea>
            );
          }}
        </Droppable>
      </DragDropContext>
      <TaskOption />
    </Wrapper>
  );
};

export default TaskDropArea;
