import { useForm } from "react-hook-form";

interface Tinput {
  nickName: string;
  email: string;
  name: string;
  phoneNumber: number;
  pw1: string;
  pw2: string;
}

const ReactHookForm = () => {
  const {
    register,
    handleSubmit,
    watch,
    formState: { errors },
    setError,
    reset,
  } = useForm<Tinput>({
    defaultValues: {
      email: "@naver.com",
    },
  });

  const onValid = (data: Tinput) => {
    if (data.pw1 !== data.pw2) {
      setError(
        "pw2",
        { message: " " },
        { shouldFocus: true }
      );
      return;
    }

    console.log(data);
    reset();
  };

  // console.log(errors);

  return (
    <>
      <form onSubmit={handleSubmit(onValid)}>
        <input
          {...register("nickName", {
            required: " ",
            minLength: { value: 2, message: "  ." },
          })}
          placeholder=""
        />
        <span>{errors.nickName?.message}</span>
        <input
          {...register("email", {
            required: " ",
            pattern: {
              value: /^[A-Za-z0-9.-_]+@naver.com$/,
              message: "  ",
            },
          })}
          placeholder=""
        />
        <span>{errors.email?.message}</span>
        <input
          {...register("name", { required: " " })}
          placeholder=""
        />
        <span>{errors.name?.message}</span>
        <input
          {...register("phoneNumber", { required: "  " })}
          placeholder=""
        />
        <span>{errors.phoneNumber?.message}</span>
        <input
          {...register("pw1", {
            required: " ",
          })}
          placeholder=""
        />
        <input
          {...register("pw2", {
            required: " ",
            validate: {
              checkEqual: (value) =>
                value.length > 0 && value === watch()?.pw1 ? true : "",
            },
          })}
          placeholder=" "
        />
        <span>
          {errors?.pw2?.message
            ? errors?.pw2?.message
            : watch()?.pw2?.length > 0
            ? watch("pw2") === watch("pw1")
              ? ""
              : ""
            : null}
        </span>
        <button>add</button>
      </form>
    </>
  );
};

export default ReactHookForm;
