export const fetchCoins = async () => {
  return (await fetch("https://api.coinpaprika.com/v1/coins")).json();
};

export const fetchCoinByID = async (coinID) => {
  return (await fetch(`https://api.coinpaprika.com/v1/coins/${coinID}`)).json();
};

export const fetCoinMarketById = async (coinID) => {
  return (
    await fetch(`https://api.coinpaprika.com/v1/tickers/${coinID}`)
  ).json();
};


export const fetchMovie = async (page: Number) => {
  const options = {
    method: "GET",
    headers: {
      accept: "application/json",
      Authorization,
    },
  };

  return (
    await fetch(
      `https://api.themoviedb.org/3/discover/movie?include_adult=false&include_video=false&language=ko-KR&page=${page}&region=KO&sort_by=popularity.desc'`,
      options
    )
  ).json();
};

export const fetchMovieDetail = async (movieID: string) => {
  const url = `https://api.themoviedb.org/3/movie/${movieID}?language=ko-KR`;
  const options = {
    method: "GET",
    headers: {
      accept: "application/json",
      Authorization,
    },
  };

  return (await fetch(url, options)).json();
};
