import {
  motion,
  Variants,
  useMotionValue,
  useTransform,
  AnimatePresence,
} from "framer-motion";
import { useEffect, useRef, useState } from "react";
import styled from "styled-components";

const Wrapper = styled.div`
  width: 100%;
  height: 100vh;
  overflow-y: scroll;
  background-image: linear-gradient(-45deg, #ff4da9, #ff33d6);
  display: grid;
  gap: 10rem;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, 1fr);
  padding: 5%;
`;

const MotionContainer = styled.div`
  display: flex;
  flex-direction: column;
  gap: 2em;
  align-items: center;
`;

const PlayBtn = styled(motion.div)`
  border-radius: 1em;
  border: none;
  background-color: white;
  padding: 0.5em 1em;
  cursor: pointer;
`;

const MotionTitle = styled.div`
  color: white;
  font-size: 22px;
`;

const Box = styled(motion.div)`
  width: 200px;
  height: 200px;
  background-color: rgba(0, 0, 0, 0.05);
  border-radius: 4em;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.2);
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
`;

const Circle = styled(motion.div)`
  width: 4em;
  height: 4em;
  border-radius: 50%;
  background-color: white;
  place-self: center;
`;

const GestureBtn = styled(motion.div)`
  width: 200px;
  height: 200px;
  background-color: rgba(255, 255, 255, 0.9);
  border-radius: 4em;
`;

const boxVariants: Variants = {
  start: {
    opacity: 0.5,
    scale: 0,
  },
  end: {
    scale: 1,
    opacity: 1,
    transition: {
      type: "spring",
      duration: 1,
      bounce: 0.6,
      delayChildren: 1,
      staggerChildren: 0.3,
    },
  },
};

const circleVariants: Variants = {
  start: {
    opacity: 0,
    y: 15,
  },
  end: {
    opacity: 2,
    y: 0,
    transition: {
      type: "spring",
      duration: 0.5,
    },
  },
};

const slideVariants: Variants = {
  start: (isCardGoingBack: boolean) => ({
    x: isCardGoingBack ? -200 : 200,
    opacity: 0,
    scale: 0,
  }),
  stand: {
    x: 0,
    opacity: 1,
    scale: 1,
    transition: {
      duration: 0.5,
    },
  },
  exit: (isCardGoingBack) => ({
    x: isCardGoingBack ? 200 : -200,
    opacity: 0,
    scale: 0,
    transition: {
      duration: 0.5,
    },
  }),
};

const Animation = () => {
  const array = ["1", "2", "3", "4"];
  const scrollContainer = useRef(null);
  const dragContainer = useRef(null);
  const [firstCounter, setFirstCounter] = useState(0);
  const [svgCounter, setSvgCounter] = useState(0);
  const [modalId, setModalIdState] = useState("");
  const [isModalOn, setModalState] = useState(false);
  const [cardNumber, setCardNumber] = useState(0);
  const [isCardGoingBack, setCardGoingBack] = useState(false);
  const [isAnimationLeaving, setAnimationLeavingState] = useState(false);
  const toggleAnimatinoLeaving = () => {
    setAnimationLeavingState((prev) => !prev);
  };

  const y = useMotionValue(0);
  const input = [0, 200];
  const output = [3, 1];
  const fill = useTransform(y, input, output);

  console.log(isAnimationLeaving);

  useEffect(() => {
    const handleEsc = (e) => {
      if (e.key === "Escape" && isModalOn) {
        setModalState(false);
      }
    };

    window.addEventListener("keydown", handleEsc);

    return () => {
      window.removeEventListener("keydown", handleEsc);
    };
  }, [isModalOn]);

  const icon: Variants = {
    hidden: {
      fill: "#ffffff",
    },
    visible: {
      fill: "#03ac00",
      transition: {
        duration: 2,
      },
    },
  };

  return (
    <Wrapper>
      <MotionContainer>
        <MotionTitle>Variants</MotionTitle>
        <Box
          key={firstCounter}
          variants={boxVariants}
          initial="start"
          animate="end"
        >
          <Circle variants={circleVariants} />
          <Circle variants={circleVariants} />
          <Circle variants={circleVariants} />
          <Circle variants={circleVariants} />
        </Box>
        <PlayBtn
          whileHover={{
            scale: 1.2,
          }}
          onClick={() => {
            setFirstCounter((count) => count + 1);
          }}
        >
          
        </PlayBtn>
      </MotionContainer>
      <MotionContainer>
        <MotionTitle>While</MotionTitle>
        <GestureBtn
          whileHover={{
            rotateZ: 90,
            scale: 1.2,
            transition: {
              type: "spring",
              duration: 0.5,
            },
          }}
          whileTap={{
            borderRadius: "100px",
          }}
        />
      </MotionContainer>
      <MotionContainer>
        <MotionTitle>Drag</MotionTitle>
        <div
          ref={dragContainer}
          style={{
            backgroundColor: "white",
            width: "300px",
            height: "300px",
            borderRadius: "4em",
            display: "grid",
            placeItems: "center",
            overflow: "hidden",
          }}
        >
          <motion.div
            style={{
              width: "100px",
              height: "100px",
              backgroundColor: "#b600c3",
              borderRadius: "2em",
            }}
            drag
            dragConstraints={dragContainer}
            dragElastic={0.1}
          />
        </div>
      </MotionContainer>
      <MotionContainer>
        <MotionTitle>Motion Value</MotionTitle>
        <div
          style={{
            display: "flex",
            justifyContent: "space-around",
            width: "300px",
          }}
        >
          <div
            style={{
              height: "200px",
              width: "200px",
              backgroundColor: "rgba(255, 255, 255, 0.5)",
              borderRadius: "10px",
              overflow: "hidden",
              position: "relative",
            }}
          >
            <motion.div
              dragConstraints={scrollContainer}
              style={{
                position: "absolute",
                top: "200px",
                width: "100%",
                height: "200px",
                backgroundColor: "white",
                scale: fill,
              }}
              transition={{ type: "spring", bounce: "1" }}
            />
          </div>

          <div
            ref={scrollContainer}
            style={{
              height: "200px",
              width: "10px",
              backgroundColor: "rgba(255, 255, 255, 0.5)",
              borderRadius: "10px",
              overflow: "hidden",
            }}
          >
            <motion.div
              drag="y"
              dragConstraints={scrollContainer}
              style={{
                y,
                width: "100%",
                height: "40px",
                backgroundColor: "white",
                borderRadius: "10px",
              }}
            />
          </div>
        </div>
      </MotionContainer>
      <MotionContainer>
        <MotionTitle>SVG</MotionTitle>
        <motion.svg
          key={svgCounter}
          fill="white"
          pathLength={1}
          viewBox="0 0 46.13 46.13"
          width="200px"
          height="200px"
          variants={icon}
          initial="hidden"
          animate="visible"
        >
          <motion.path d="M41.018,0.365c-0.38-0.342-0.688-0.461-1.064-0.282C35.202,2.349,28.245,7.011,23,16.148 c3.04,4.616,5.675,10.559,7.057,18.125c0.181-7.561,1.681-23.859,10.856-32.812C41.212,1.171,41.255,0.707,41.018,0.365z" />
          <motion.path d="M9.673,6.905C9.531,6.811,9.369,6.766,9.207,6.766c-0.231,0-0.461,0.094-0.628,0.275c-0.282,0.307-0.3,0.773-0.044,1.101 c9.251,11.848,6.688,32.297,5.946,37.004c-0.039,0.247,0.031,0.498,0.193,0.688c0.162,0.188,0.399,0.299,0.648,0.299h11.908 c0.229,0,0.446-0.091,0.606-0.253s0.249-0.381,0.246-0.609C27.806,22.242,16.498,11.376,9.673,6.905z" />
          <motion.path d="M2.464,27.554c-0.165-0.108-0.385-0.081-0.519,0.064c-0.134,0.146-0.143,0.366-0.021,0.522 c4.387,5.617,3.172,15.312,2.819,17.543C4.724,45.801,4.759,45.909,4.834,46c0.077,0.09,0.19,0.132,0.308,0.132h5.645 c0.107,0,0.211-0.034,0.287-0.11s0.118-0.176,0.117-0.284C11.061,34.82,5.701,29.674,2.464,27.554z" />
          <motion.path d="M44.146,20.59c-0.186-0.201-0.486-0.239-0.716-0.089c-4.463,2.923-11.854,10.028-12.036,25.087 c-0.003,0.147,0.059,0.281,0.161,0.388c0.105,0.106,0.246,0.156,0.397,0.156h7.785c0.164,0,0.317-0.062,0.424-0.187 c0.104-0.124,0.15-0.282,0.126-0.443c-0.485-3.078-2.161-16.447,3.889-24.195C44.344,21.093,44.331,20.791,44.146,20.59z" />
        </motion.svg>
        <PlayBtn
          whileHover={{
            scale: 1.2,
          }}
          onClick={() => {
            setSvgCounter((count) => count + 1);
          }}
        >
          
        </PlayBtn>
      </MotionContainer>
      <div
        style={{
          color: "white",
          fontSize: "22px",
          textTransform: "uppercase",
          margin: "auto",
        }}
      >
        <p>watch down below &darr;</p>
      </div>
      <MotionContainer
        style={{
          gridColumn: "span 2",
          gridRow: "span 2",
          gap: "1rem",
        }}
      >
        <div
          style={{
            display: "flex",
            gap: "1rem",
          }}
        >
          <motion.div
            layoutId={array[0]}
            style={{
              backgroundColor: "white",
              height: "300px",
              width: "500px",
              borderRadius: "20px",
            }}
            onClick={() => {
              setModalIdState(array[0]);
              setModalState(true);
            }}
          ></motion.div>
          <motion.div
            layoutId={array[1]}
            style={{
              backgroundColor: "white",
              height: "300px",
              width: "300px",
              borderRadius: "20px",
            }}
            onClick={() => {
              setModalIdState(array[1]);
              setModalState(true);
            }}
          ></motion.div>
        </div>
        <div
          style={{
            display: "flex",
            gap: "1rem",
          }}
        >
          <motion.div
            layoutId={array[2]}
            style={{
              backgroundColor: "white",
              height: "300px",
              width: "300px",
              borderRadius: "20px",
            }}
            onClick={() => {
              setModalIdState(array[2]);
              setModalState(true);
            }}
          ></motion.div>
          <motion.div
            layoutId={array[3]}
            style={{
              backgroundColor: "white",
              height: "300px",
              width: "500px",
              borderRadius: "20px",
            }}
            onClick={() => {
              setModalIdState(array[3]);
              setModalState(true);
            }}
          ></motion.div>
        </div>
      </MotionContainer>
      <MotionContainer
        style={{
          gridRow: "span 2",
          display: "flex",
          flexDirection: "column",
          margin: "auto",
          position: "relative",
        }}
      >
        <AnimatePresence
          initial={false}
          custom={isCardGoingBack}
          onExitComplete={toggleAnimatinoLeaving}
        >
          <motion.div
            style={{
              position: "absolute",
              top: "-220px",
              left: "-150px",
              backgroundColor: "white",
              width: "300px",
              height: "200px",
              borderRadius: "20px",
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
            }}
            key={cardNumber}
            variants={slideVariants}
            custom={isCardGoingBack}
            initial="start"
            animate="stand"
            exit="exit"
          >
            {cardNumber}
          </motion.div>
        </AnimatePresence>
        <div
          style={{
            display: "flex",
          }}
        >
          <PlayBtn
            onClick={() => {
              if (isAnimationLeaving) return;
              setAnimationLeavingState(true);
              setCardGoingBack(true);
              setCardNumber((prevCardNumber) => {
                return prevCardNumber - 1;
              });
            }}
          >
            
          </PlayBtn>
          <PlayBtn
            onClick={() => {
              if (isAnimationLeaving) return;
              setAnimationLeavingState(true);
              setCardGoingBack(false);
              setCardNumber((prevCardNumber) => {
                return prevCardNumber + 1;
              });
            }}
          >
            
          </PlayBtn>
        </div>
      </MotionContainer>

      <AnimatePresence>
        {isModalOn ? (
          <motion.div
            style={{
              position: "absolute",
              left: "0",
              top: "0",
              overflow: "scroll",
              width: "100%",
              height: "100%",
              backgroundColor: "rgba(0, 0, 0, 0.2)",
              display: "grid",
              placeItems: "center",
              zIndex: "100",
            }}
            initial={{ backgroundColor: "rgba(0, 0, 0, 0)" }}
            animate={{ backgroundColor: "rgba(0, 0, 0, 0.5)" }}
            exit={{ backgroundColor: "rgba(0, 0, 0, 0)" }}
            onClick={() => setModalState(false)}
            onKeyDown={(e) => {
              console.log(e);
            }}
          >
            <motion.div
              layoutId={modalId}
              style={{
                backgroundColor: "white",
                height: "300px",
                width: "400px",
                borderRadius: "20px",
              }}
              onClick={(e) => e.stopPropagation()}
            >
              <motion.div
                style={{
                  padding: "20px",
                  display: "flex",
                  justifyContent: "end",
                }}
              >
                <motion.span
                  style={{
                    fontSize: "22px",
                    color: "rgba(0, 0, 0, 0.5)",
                    cursor: "pointer",
                  }}
                  onClick={() => setModalState(false)}
                >
                  &times;
                </motion.span>
              </motion.div>
            </motion.div>
          </motion.div>
        ) : null}
      </AnimatePresence>
    </Wrapper>
  );
};

export default Animation;
