import { useQuery } from "@tanstack/react-query";
import styled from "styled-components";
import { fetCoinMarketById, fetchCoinByID } from "../api";
import { Link, useParams } from "react-router-dom";
import { useRecoilValue, useSetRecoilState } from "recoil";
import { darkMode } from "../atoms";

const Container = styled.div`
  width: 480px;
  height: 100vh;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: ${(props) => props.theme.fontColor};
`;

const Header = styled.div`
  padding: 3em;
  padding-bottom: 0;
  display: grid;
  place-items: center;
  & > h1 {
    padding-top: 0.5em;
    font-size: 3rem;
  }
  img {
    width: 5em;
  }
`;

const Options = styled.div`
  width: 480px;
  padding: 1em;
  display: flex;
  justify-content: end;
`;

const Button = styled.button`
  background-color: ${(props) => props.theme.cardColor};
  color: inherit;
  border: 1.5px solid ${(props) => props.theme.borderColor};
  width: 4em;
  padding: 0.5em 0em;
  border-radius: 1em;
  transition: ease-in-out 0.2s;
  cursor: pointer;
  &:hover {
    background-color: ${(props) => props.theme.hoverBgColor};
    border: 1.5px solid ${(props) => props.theme.hoverBgColor};
  }
`;

const CoinInfo = styled.div`
  background-color: ${(props) => props.theme.cardColor};
  border: 1px solid ${(props) => props.theme.borderColor};
  display: grid;
  width: 480px;
  grid-template-columns: repeat(3, 1fr);
  gap: 1em;
  padding: 1em;
  border-radius: 1em;
  color: ${(props) => props.theme.fontColor};
  transition: ease-in-out 0.2s;
  & > div {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1em;
    & > span {
      font-size: 19px;
      text-transform: uppercase;
    }
  }
`;

const Discription = styled.div`
  padding: 0.5em;
  font-size: 20px;
  line-height: 1.3em;
`;

const MarketInfo = styled(CoinInfo)`
  grid-template-columns: repeat(2, 1fr);
`;

const Home = styled.div`
  padding: 2em;
`;

interface Tparams {
  coinID: string;
}

interface Tcoin {
  id: string;
  name: string;
  symbol: string;
  rank: number;
  is_new: boolean;
  is_active: boolean;
  type: string;
  logo: string;
  tags: object;
  team: object;
  description: string;
  message: string;
  open_source: boolean;
  started_at: string;
  development_status: string;
  hardware_wallet: boolean;
  proof_type: string;
  org_structure: string;
  hash_algorithm: string;
  links: object;
  links_extended: object;
  whitepaper: object;
  first_data_at: string;
  last_data_at: string;
}

interface TcoinMarket {
  id: string;
  name: string;
  symbol: string;
  rank: number;
  total_supply: number;
  max_supply: number;
  beta_value: number;
  first_data_at: string;
  last_updated: string;
  quotes: {
    USD: {
      price: number;
      volume_24h: number;
      volume_24h_change_24h: number;
      market_cap: number;
      market_cap_change_24h: number;
      percent_change_15m: number;
      percent_change_30m: number;
      percent_change_1h: number;
      percent_change_6h: number;
      percent_change_12h: number;
      percent_change_24h: number;
      percent_change_7d: number;
      percent_change_30d: number;
      percent_change_1y: number;
      ath_price: number;
      ath_date: string;
      percent_from_price_ath: number;
    };
  };
}

const Coin = () => {
  const isDarkMode = useRecoilValue(darkMode);
  const setIsDarkMode = useSetRecoilState(darkMode);
  const { coinID } = useParams<Tparams>();

  const { isLoading: coinLoading, data: coin } = useQuery<Tcoin>({
    queryKey: ["coinInfo", coinID],
    queryFn: () => fetchCoinByID(coinID),
  });

  const { isLoading: coinMarketLoading, data: coinMarket } =
    useQuery<TcoinMarket>({
      queryKey: ["coinMarket", coinID],
      queryFn: () => fetCoinMarketById(coinID),
    });

  return (
    <Container>
      {coinLoading || coinMarketLoading ? (
        "Loading..."
      ) : (
        <>
          <Header>
            <img
              src={`https://static.coinpaprika.com/coin/${coin.id}/logo.png`}
              alt="coin img"
            />
            <h1>{coin.name}</h1>
          </Header>
          <Options>
            <Button
              onClick={() => {
                setIsDarkMode((curr) => !curr);
              }}
            >
              {isDarkMode ? "" : ""}
            </Button>
          </Options>
          <CoinInfo>
            <div>
              <span>rank</span>
              {coin.rank}
            </div>
            <div>
              <span>symbol</span>
              {coin.symbol}
            </div>
            <div>
              <span>state</span>
              {coin.development_status}
            </div>
          </CoinInfo>
          <Discription>
            {coin.description ? coin.description : "no description"}
          </Discription>
          <MarketInfo>
            <div>
              <span>price</span>
              {coinMarket.quotes.USD.price} $
            </div>
            <div>
              <span>volume 24h</span>
              {coinMarket.quotes.USD.volume_24h}
            </div>
          </MarketInfo>
        </>
      )}
      <Home>
        <Link to={"/"}></Link>
      </Home>
    </Container>
  );
};

export default Coin;
