import { useQuery } from "@tanstack/react-query";
import styled from "styled-components";
import { fetchCoins } from "../api";
import { Link } from "react-router-dom";
import { useRecoilValue, useSetRecoilState } from "recoil";
import { darkMode } from "../atoms";

const Container = styled.div`
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: ${(props) => props.theme.fontColor};
`;

const Header = styled.div`
  padding-top: 3em;
  display: grid;
  place-items: center;
  & > h1 {
    font-size: 3rem;
  }
`;

const Options = styled.div`
  width: 480px;
  padding: 1em;
  display: flex;
  justify-content: end;
`;

const Button = styled.button`
  background-color: ${(props) => props.theme.cardColor};
  color: inherit;
  border: 1.5px solid ${(props) => props.theme.borderColor};
  width: 4em;
  padding: 0.5em 0em;
  border-radius: 1em;
  transition: ease-in-out 0.2s;
  cursor: pointer;
  &:hover {
    background-color: ${(props) => props.theme.hoverBgColor};
    border: 1.5px solid ${(props) => props.theme.hoverBgColor};
  }
`;

const CoinList = styled.ul`
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 10px;
`;

const CoinItem = styled.li`
  background-color: ${(props) => props.theme.cardColor};
  border: 1.5px solid ${(props) => props.theme.borderColor};
  border-radius: 1em;
  width: 480px;
  transition: ease-in-out 0.2s;
  position: relative;
  font-size: 1.1rem;
  & > a {
    display: block;
    padding: 2em;
    padding-left: 5em;
    transform: translateY(0.1em);
  }
  & > img {
    position: absolute;
    top: 1em;
    left: 1em;
    width: 3em;
    height: 3em;
  }
  &:hover {
    background-color: ${(props) => props.theme.hoverBgColor};
    transform: scale(103%);
    border: none;
    border: 1.5px solid ${(props) => props.theme.hoverBgColor};
  }
`;

interface Tcoin {
  id: string;
  name: string;
  symbol: string;
  rank: number;
  is_new: boolean;
  is_active: boolean;
  type: string;
}

const Coins = () => {
  const isDarkMode = useRecoilValue(darkMode);
  const setIsDarkMode = useSetRecoilState(darkMode);

  const { isLoading, data: coins } = useQuery<Tcoin[]>({
    queryKey: ["allCoins"],
    queryFn: fetchCoins,
  });

  return (
    <>
      <Container>
        <Header>
          <h1></h1>
        </Header>
        <Options>
          <Button
            onClick={() => {
              setIsDarkMode((curr) => !curr);
            }}
          >
            {isDarkMode ? "" : ""}
          </Button>
        </Options>
        <CoinList>
          {isLoading
            ? "Loading..."
            : coins.slice(0, 100).map((coin) => {
                return (
                  <CoinItem key={coin.id}>
                    <img
                      src={`https://static.coinpaprika.com/coin/${coin.id}/logo.png`}
                      alt="coin img"
                    />
                    <Link to={`coin/${coin.id}`}>{coin.name}</Link>
                  </CoinItem>
                );
              })}
        </CoinList>
      </Container>
    </>
  );
};

export default Coins;
