import { useRecoilState } from "recoil";
import { hoursState, minutesState } from "../atoms";


const MinutesToHours = () => {
  const [minutes, setMinutes] = useRecoilState(minutesState);
  const [hours, setHours] = useRecoilState(hoursState);

  return <>
    <input value={minutes} onChange={(event) => setMinutes(+event.currentTarget.value)}/>
    <input value={hours} onChange={(event) => setHours(+event.currentTarget.value)}/>
  </>
}

export default MinutesToHours;