import { useForm } from "react-hook-form";
import { useRecoilState, useRecoilValue, useSetRecoilState } from "recoil";
import {
  TtodoProcess,
  todoListFilterState,
  filteredTodoListState,
  todosAtom,
} from "../atoms";
import { useState } from "react";

interface TtodoForm {
  todo: string;
}

interface TtodoUpdateForm {
  newTodo: string;
}

interface TtodoProps {
  todoKey: string;
  setIsUpdate?: (newIsUpdate: boolean) => void;
}

export const TodoForm = () => {
  const setTodos = useSetRecoilState(todosAtom);
  const {
    register,
    handleSubmit,
    reset,
    formState: { errors },
    setError,
  } = useForm<TtodoForm>();

  const addTodo = (todo: string) => {
    setTodos((prevTodos) => {
      const newTodos = { ...prevTodos };
      const key = new Date().getMilliseconds().toString();
      newTodos[key] = {
        key,
        todo,
        process: TtodoProcess.DONE,
      };
      return newTodos;
    });
  };

  const onValid = ({ todo }: TtodoForm) => {
    reset();

    if (todo.length < 1) {
      setError(
        "todo",
        { type: "minLength", message: "write todo!" },
        { shouldFocus: true }
      );
      return;
    }

    addTodo(todo);
  };

  return (
    <form onSubmit={handleSubmit(onValid)}>
      <input {...register("todo")} />
      <span>{errors?.todo?.message}</span>
      <button type="submit">add</button>
    </form>
  );
};

export const Todos = () => {
  const setFilter = useSetRecoilState(todoListFilterState);
  const todos = useRecoilValue(filteredTodoListState);

  // useEffect(() => {
  //   setFilter(TtodoProcess.TO_DO)
  // })

  return (
    <>
      <ul>
        {todos.length === 0 ? (
          <span>no {}! </span>
        ) : (
          todos.map((todo) => {
            return <TodoItem todoKey={todo.key} />;
          })
        )}
      </ul>
    </>
  );
};

const TodoItem = ({ todoKey }: TtodoProps) => {
  const [isUpdate, setIsupdate] = useState(false);
  const todos = useRecoilValue(todosAtom);

  const onUpdate = () => {
    setIsupdate(true);
  };

  return (
    <li key={todoKey}>
      {isUpdate ? (
        <UpdateTodoForm todoKey={todoKey} setIsUpdate={setIsupdate} />
      ) : (
        <span>{todos[todoKey].todo}</span>
      )}
      {isUpdate ? null : (
        <>
          <CancelTodo todoKey={todoKey} />
          <DoneTodo todoKey={todoKey} />
          <button onClick={onUpdate}></button>
        </>
      )}
    </li>
  );
};

const CancelTodo = ({ todoKey }: TtodoProps) => {
  const setTodos = useSetRecoilState(todosAtom);

  const onCancel = (key: string) => {
    setTodos((prevTodos) => {
      const newTodos = { ...prevTodos };
      delete newTodos[key];
      return newTodos;
    });
  };
  return <button onClick={() => onCancel(todoKey)}></button>;
};

const DoneTodo = ({ todoKey }: TtodoProps) => {
  const setDones = useSetRecoilState(todosAtom);

  const onDone = (key: string) => {
    setDones((prevTodos) => {
      const newTodos = { ...prevTodos };
      const newTodoItem = {
        key,
        todo: prevTodos[key].todo,
        process: TtodoProcess.DONE,
      };
      newTodos[key] = newTodoItem;
      return newTodos;
    });
  };

  return <button onClick={() => onDone(todoKey)}></button>;
};

const UpdateTodoForm = ({ todoKey, setIsUpdate }: TtodoProps) => {
  const { register, handleSubmit } = useForm<TtodoUpdateForm>();
  const [todos, setTodos] = useRecoilState(todosAtom);

  const updateTodo = (newTodo: string) => {
    setTodos((prevTodos) => {
      const newTodos = { ...prevTodos };
      newTodos[todoKey].todo = newTodo;
      return newTodos;
    });
  };

  const onSubmit = ({ newTodo }: TtodoUpdateForm) => {
    setIsUpdate(false);
    updateTodo(newTodo);
  };

  return (
    <form onSubmit={handleSubmit(onSubmit)}>
      <input {...register("newTodo")} placeholder={todos[todoKey].todo} />
      <button type="submit">update</button>
    </form>
  );
};

export const Dones = () => {
  const setFilter = useSetRecoilState(todoListFilterState);
  const dones = useRecoilValue(filteredTodoListState);

  useState(() => {
    setFilter(TtodoProcess.DONE)
  })

  return (
    <ul>
      {dones.length === 0
        ? null
        : dones.map((done) => {
            return <li key={done.key}>{done.todo}</li>;
          })}
    </ul>
  );
};
