import { atom, selector } from "recoil";

export const darkMode = atom({
  key: "darkModeState",
  default: false,
});

export enum TtodoProcess {
  TO_DO = "TO_DO",
  DOING = "DOING",
  DONE = "DONE",
}

export interface Ttodo {
  key: string;
  todo: string;
  process?: TtodoProcess;
}

export interface Ttask {
  name: string;
  todos: Ttodo[];
}

interface Ttodos {
  [key: string]: Ttodo;
}

export const todoListFilterState = atom<TtodoProcess>({
  key: "TodoListFilter",
  default: TtodoProcess.TO_DO,
});

export const todosAtom = atom<Ttodos>({
  key: "todos",
  default: {},
});

export const filteredTodoListState = selector<Ttodo[]>({
  key: "FilteredTodoList",
  get: ({ get }) => {
    const filter = get(todoListFilterState);
    const todos = get(todosAtom);

    return Object.values(todos).filter((todo) => todo.process === filter);
  },
});

export const minutesState = atom<number>({
  key: "Minutes",
  default: 0,
});

export const hoursState = selector<number>({
  key: "Hours",
  get: ({ get }) => {
    const minutes = get(minutesState);
    return minutes / 60;
  },
  set: ({ set }, newValue) => {
    set(minutesState, Number(newValue) * 60);
  },
});

// these are exist for drag and drop tasks
export enum TdragItem {
  Task = "Task",
  Todo = "Todo",
}

export const taskState = atom<Ttask[]>({
  key: "TodoOrderState",
  default: [
    {
      name: "To Do",
      todos: [
        { key: "1", todo: "a" },
        { key: "2", todo: "b" },
      ],
    },
    {
      name: "Doing",
      todos: [
        { key: "3", todo: "c" },
        { key: "4", todo: "d" },
      ],
    },
    {
      name: "Done",
      todos: [{ key: "5", todo: "e" }],
    },
  ],
});

export const taskFilterState = atom<string>({
  key: "taskFilterState",
  default: "To Do",
});

export const selectedTaskState = selector<Ttask>({
  key: "filteredTaskState",
  get: ({ get }) => {
    const filter = get(taskFilterState);
    const tasks = get(taskState);

    return tasks.find((task) => task.name === filter);
  },
  set: ({ set, get }, newTask: Ttask) => {
    const tasks = get(taskState);
    const filter = get(taskFilterState);

    const updateTask = tasks.find((task) => task.name === filter);
    const updateIndex = tasks.indexOf(updateTask);

    const newTasks = [...tasks];
    newTasks.splice(updateIndex, 1, newTask);

    set(taskState, newTasks);
  },
});

export const taskDraggingState = atom<boolean>({
  key: "dragTasktate",
  default: false,
});

export const todoDraggingState = atom<boolean>({
  key: "dragTodoState",
  default: false,
});

export const modalOpenState = atom<boolean>({
  key: "modalOpenState",
  default: false,
});

// this is for naflix

export const slideItemNumberState = atom<number>({
  key: "slideItemNumber",
  default: 1,
});
