import { Draggable } from "react-beautiful-dnd";
import {
  Ttask,
  modalOpenState,
  selectedTaskState,
  taskFilterState,
  taskState,
} from "../atoms";
import { useRecoilState, useRecoilValue, useSetRecoilState } from "recoil";
import styled from "styled-components";
import React, { useState } from "react";
import TodoDropArea from "./TodoDropArea";

interface TwindowProps {
  isDragging: boolean;
}

const Window = styled.div<TwindowProps>`
  background-color: ${(props) =>
    props.isDragging ? "tomato" : props.theme.borderColor};
  display: flex;
  flex-direction: column;
  width: 300px;
  min-height: 300px;
  height: max-content;
  gap: 1em;
  border-radius: 1em;
  padding-top: 1em;
  & > div:first-child {
    text-align: center;
  }
`;

const WindowHandle = styled.div``;

interface TmodalProps {
  isOpen: boolean;
}

const Add = styled.div`
  display: inline-block;
  width: 1em;
  height: 1em;
  text-align: center;
  font-size: 20px;
  background-color: white;
  border-radius: 1em;
  cursor: pointer;
`;

const ModalWrapper = styled.div<TmodalProps>`
  display: ${(props: { isOpen: boolean }) => (props.isOpen ? "block" : "none")};
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.2);
`;

const ModalContent = styled.form`
  background-color: #ded0b6;
  margin: 15% auto;
  padding: 1em;
  border-radius: 1em;
  width: 80%;
  max-width: 400px;
`;

const CloseButton = styled.span`
  display: inline-block;
  color: #aaa;
  font-size: 28px;
  font-weight: bold;
  &:hover {
    color: black;
    text-decoration: none;
    cursor: pointer;
  }
`;

const ModalInput = styled.input`
  width: calc(100% - 40px);
  border: none;
  margin-bottom: 10px;
`;

const SubmitButton = styled.button`
  background-color: #4caf50;
  color: white;
  padding: 10px 15px;
  border: none;
  border-radius: 1em;
  cursor: pointer;
`;

const Task = () => {
  const tasks = useRecoilValue(taskState);
  const [newTodo, setNewTodo] = useState("");
  const [modalSubmitState, setmodalSubmitState] = useState(false);
  const setTaskFilterState = useSetRecoilState(taskFilterState);
  const setSelectedTaskState = useSetRecoilState(selectedTaskState);
  const [isModalOpen, setModalOpenState] = useRecoilState(modalOpenState);

  const openModal = () => {
    setModalOpenState(true);
  };

  const closeModal = () => {
    setModalOpenState(false);
  };

  return (
    <>
      {tasks.map(({ name: taskName }: Ttask, index) => {
        return (
          <Draggable
            draggableId={`task_${index}`}
            index={index}
            key={`task_${index}`}
          >
            {(provided, snapshot) => {
              return (
                <Window
                  ref={provided.innerRef}
                  {...provided.draggableProps}
                  isDragging={snapshot.isDragging}
                >
                  <WindowHandle {...provided.dragHandleProps}>
                    {taskName}
                    <Add
                      onClick={() => {
                        openModal();
                        setTaskFilterState(taskName);
                      }}
                    >
                      +
                    </Add>
                    {/* <AddTodoBtn /> */}
                  </WindowHandle>
                  <TodoDropArea taskName={taskName} taskIndex={index} />
                  {isModalOpen ? (
                    <ModalWrapper isOpen={isModalOpen}>
                      {modalSubmitState ? (
                        <ModalContent>
                          <CloseButton
                            onClick={() => {
                              closeModal();
                              setmodalSubmitState(false);
                            }}
                          >
                            &times;
                          </CloseButton>
                          <span></span>
                        </ModalContent>
                      ) : (
                        <ModalContent
                          onSubmit={(e) => {
                            e.preventDefault();
                            setSelectedTaskState((prevTask) => {
                              const newTodos = [...prevTask.todos];
                              newTodos.push({
                                key: new Date().getMilliseconds + "",
                                todo: newTodo,
                              });
                              const newTask = { ...prevTask };
                              newTask.todos = newTodos;
                              return newTask;
                            });
                            setmodalSubmitState(true);
                          }}
                        >
                          <CloseButton onClick={closeModal}>
                            &times;
                          </CloseButton>
                          <p>to do </p>
                          <br />
                          <ModalInput
                            type="text"
                            value={newTodo}
                            onChange={(e) => setNewTodo(e.target.value)}
                            placeholder="to do"
                            maxLength={20}
                          />
                          <SubmitButton type="submit"></SubmitButton>
                        </ModalContent>
                      )}
                    </ModalWrapper>
                  ) : null}
                </Window>
              );
            }}
          </Draggable>
        );
      })}
    </>
  );
};

export default React.memo(Task);
