import { DraggableProvided, Droppable } from "react-beautiful-dnd";
import styled from "styled-components";
import { TdragItem, todoDraggingState } from "../atoms";
import { useRecoilValue } from "recoil";
import Todos from "./Todos";

interface TdragableWindowProps {
  taskName: string;
  taskIndex: number;
  draggableProvided?: DraggableProvided;
}

type TdropPlaceProps = {
  draggingFromThisWith: boolean;
  isDraggingOver: boolean;
  isDragging: boolean;
};

const DropPlace = styled.div<TdropPlaceProps>`
  display: flex;
  flex-direction: column;
  gap: 1em;
  background-color: white;
  min-height: 300px;
  border-bottom-left-radius: 1em;
  border-bottom-right-radius: 1em;
  padding: 1em 1em;
  background-color: ${(props) =>
    props.draggingFromThisWith
      ? "tomato"
      : props.isDraggingOver
      ? "teal"
      : props.isDragging
      ? "grey"
      : "#DED0B6"};
  transition: background-color ease-in-out 0.3s;
`;

const TodoDropArea = ({ taskName, taskIndex }: TdragableWindowProps) => {
  const isDragTodo = useRecoilValue(todoDraggingState);

  return (
    <Droppable droppableId={taskName} type={TdragItem.Todo}>
      {(provided, snapshot) => {
        return (
          <DropPlace
            ref={provided.innerRef}
            {...provided.droppableProps}
            draggingFromThisWith={Boolean(snapshot.draggingFromThisWith)}
            isDraggingOver={snapshot.isDraggingOver}
            isDragging={isDragTodo}
          >
            <Todos taskName={taskName} taskIndex={taskIndex} />
            {provided.placeholder}
          </DropPlace>
        );
      }}
    </Droppable>
  );
};

export default TodoDropArea;
